.activitiesCarousel .carousel-control-prev-icon {
  width: 3vw;
}
.activitiesCarousel .carousel-indicators {
  display: none !important;
}

.slideActivities {
  width: 100%;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-size: cover;
  background-position: center;
}

@media only screen and (max-width: 650px) {
  .slideActivities {
    width: 100%;
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-size: cover;
    background-position: center;
  }
}

/*COMMENTS SECTION*/
.comment-section {
  margin-top: 30px;
  padding: 10px;
}

.comment-section .image-holder {
  float: left;
  margin-right: 15px;
}

.comment-section .image-holder img {
  height: 50px;
  width: 50px;
  border-radius: 100%;

  object-fit: cover;
}

.comment-section .comment-desc {
  height: 1%;
  overflow: hidden;
}

.comment-section .comment-desc .user-desv .userlinks {
  display: inline-block;
  margin-right: 20px;
  vertical-align: top;
  margin-bottom: 5px;
}

.comment-section .comment-desc .user-desv .user-name {
  font-size: 14px;
  color: black;
}

.comment-section .comment-desc .user-desv a {
  color: #f7931d;
  font-size: 13px;
  text-decoration: none;
}

.comment-section .comment-desc .user-desv .muted-txt {
  color: black;
  font-size: 13px;
}

.comment-section .comment-desc .likes-sec {
  display: inline-block;
  vertical-align: top;
}

.comment-section .comment-desc .likes-sec img {
  width: 15px;
  height: 15px;
  vertical-align: top;
}

.comment-section .comment-desc .likes-sec i {
  font-size: 20px;
  color: black;
  text-decoration: none;
  display: inline-block;
  vertical-align: top;
  margin-right: 10px;
}

.comment-section .comment-desc .questions {
  font-size: 22px;
  color: black;
  margin-top: 5px;
}

.comments-section {
  background-color: #d0bdf4;
  /* padding: 2vh 2vw; */
}

.form-control {
  background-color: #d0bdf4 !important;
}
label.form-label {
  color: black !important;
  font-size: 1.5vw;
}

@media only screen and (max-width: 950px) {
  label.form-label {
    font-size: 3vw;
  }
}

